import React from "react";
import Chess from "./Chess";

function Sample() {
  const sampleCode = `componentDidMount() {
    fetch("https://qunliw.github.io/chess.json")
      .then((resp) => resp.json())
      .then((chessResp) => {
        this.setState({ chessData: chessResp });
      });
  }`;
  return (
    <div>
      <Chess />
      <div className="container">
        <section id="main">
          <h1>Sample JS code</h1>
          <p>{sampleCode}</p>
        </section>
        <aside id="sidebar">
          <p>
            Why Single Page Applications (SPAs)? A much-improved user experience
            due to dynamically rewriting the current web page with new data
            without reloading full page
          </p>
        </aside>
      </div>
    </div>
  );
}

export default Sample;
