import React from "react";

class Chess extends React.Component {
  constructor() {
    super();
    this.state = {
      showChess: false,
      chessData: [],
    };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }

  componentDidMount() {
    fetch("https://qunliw.github.io/chess.json")
      .then((resp) => resp.json())
      .then((chessResp) => {
        this.setState({ chessData: chessResp });
      });
  }

  handleToggleClick() {
    this.setState((state) => ({
      showChess: !state.showChess,
    }));
  }

  render() {
    const { showChess, chessData } = this.state;
    return (
      <section id="showcase">
        <div className="container">
          <p>SPA, REST and conditional rendering</p>
          <button id="btn" onClick={this.handleToggleClick}>
            {showChess
              ? "Hide Chess Opening Info"
              : "Show Openings of chess players"}
          </button>
          {showChess && (
            <table className="table table-striped table-sm">
              <tbody>
                <tr>
                  <th>Name</th>
                  <th>Age</th>
                  <th>White Opening</th>
                  <th>Black Opening</th>
                </tr>
                {chessData.map((player) => (
                  <tr key={player.name}>
                    <td>{player.name}</td>
                    <td>{player.age}</td>
                    <td>{player.openings.white}</td>
                    <td>{player.openings.black}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </section>
    );
  }
}

export default Chess;
